
import XHeader from 'vux/src/components/x-header/index.vue'
import Step from 'vux/src/components/step/step.vue'
import StepItem from 'vux/src/components/step/step-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Toast from 'vux/src/components/toast/index.vue'


import {
jsonpReturn, //处理返回的数据
checkRequest,
formatDate
}
from '@/libs/util'

import {
mapGetters
} from 'vuex'

import {
ChangePassword, //发送短信验证码
TreasureDetail,
TreasureJoin
}
from '@/api/user'

import Commodity from "#/commodity";


export default {
data() {
return {
id: null,
swiper: 0,
time: (new Date()).toLocaleDateString(),
img: require('@/assets/commodity.png'),
details: require('@/assets/details.png'),
wechat: require('@/assets/wechat.svg'),
friend: require('@/assets/friend.svg'),
step: 0,
show: false,
show2: false,
show3: false,
toast: false,
toastText: '',
num: 1,
commodity: {},
type: '',
imgList: [],
time: {
a: '',
b: '',
c: '',
d: ''
},
timer: null
}
},
computed: {
...mapGetters([
'username',
'telphone',
'token',
'url'
])
},
components: {
XHeader,
Commodity,
Step,
StepItem,
XButton,
Actionsheet,
Popup,
Divider,
Group,
Cell,
Flexbox,
FlexboxItem,
Toast
},
methods: {
init(id) {
TreasureDetail(this.token, id).then(res => {
let data = checkRequest(res, false)
this.commodity = data
this.commodity.detail_img.forEach(item => {
this.imgList.push(this.url + item)
})
if (data.type_id == 1) {
this.type = '未开始'
this.timeFun(this.commodity.end)
} else if (data.type_id == 2) {
this.type = '进行中'
this.timeFun(this.commodity.end)
} else if (data.type_id == 3) {
this.type = '待开奖'
} else {
this.type = '已结束'
}
console.log(this.type);
})
},
link() {
this.$router.push('commodityDetail')
},
timeFun(tim) {
this.timer = setInterval(() => {
let now = Date.parse(new Date())
let time = (Date.parse(tim) - now)
let a = new Date(time).getDay()
let b = new Date(time).getHours()
let c = new Date(time).getMinutes()
let d = new Date(time).getSeconds()
this.time.a = a
this.time.b = b
this.time.c = c
this.time.d = d
}, 1000)
},
wechatShare() {
alert("微信")
},
circleShare() {
alert("朋友圈")
},
join() {
if (this.commodity.is_join == 1) {
this.toast = true
this.toastText = '您已参与过该活动!'
} else {
this.show2 = true
}
},
submit() {
if (this.token) {
TreasureJoin(this.token, this.id).then(res => {
let data = checkRequest(res, false)
if (data) {
this.toast = true
this.toastText = '参与成功!'
} else {
this.toast = true
this.toastText = '余额不足,请先充值!'
}
console.log(checkRequest(res, false));
})
} else {
this.$router.push('login')
}
}
},
mounted() {
console.log(this.$route.query.id);
this.id = this.$route.query.id
this.init(this.id)
},
beforeDestroy() {
if (this.timer) {
clearInterval(this.timer);
}
}
}
